* Services page
* animation
***********************/
:root {
  --loanlift-primary-bg: #061525;
  --loanlift-secondary-bg: #f2f4f8;
  --loanlift-light-bg: #e7e7e7;
  --loanlift-blue-light-bg: #d5dfed;
  --loanlift-text-black: #0f1011;
  --loanlift-text-grey: #bebebe;
  --loanlift-text-dark-grey: #5a5a5a;
  --loanlift-text-light-grey: #c2c2c2;
  --loanlift-text-light: #ffffff;
  --loanlift-border-dark: #5e5e5e;
  --loanlift-border-light: #ebebeb;
  --loanlift-gradient-primary: radial-gradient(
    53% 38.84% at 72% 50%,
    #be5518 0%,
    #061525 100%
  );
  --loanlift-gradient-primary: linear-gradient(
    69.32deg,
    #061525 20.87%,
    #be5518 101.05%
  );
  --loanlift-body-font: "DM Sans", system-ui;
  --loanlift-heading-font: "Wix Madefor Display", system-ui;

  /*--------- Caprim Colors ----------*/

  /* Primary Colors*/
  --caprim-primary-gunmetal-gray: #37464c;
  --caprim-primary-royal-blue: #343faa;
  --caprim-primary-coral-orange: #f96346;

  /* Secondary or complementary colors*/
  --caprim-secondary-beige: #f5f5dc;
  --caprim-secondary-yellow: #ffc44c;
  --caprim-secondary-blue-light: #4686ca;
  --caprim-seconday-green: #30ab97;

  /*------ Caprim fonts --------*/
  --caprim-body-font: "Poppins", system-ui;
  --caprim-heading-font: "Baloo Bhaijaan 2", system-ui;
}
html {
  scroll-behavior: smooth;
}
/***********************
* Common
***********************/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: var(--caprim-body-font);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0;
  color: var(--caprim-primary-gunmetal-gray);
  text-transform: none;
  font-style: normal;
}
img {
  max-width: 100%;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  line-height: 30px;
  text-decoration: none;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;
  margin: 0px;
  font-family: var(--caprim-heading-font);
  color: var(--caprim-primary-royal-blue);
}
h1 {
  color: var(--caprim-primary-coral-orange);
  font-size: 65px;
  font-weight: 600;
  line-height: 80px;
  letter-spacing: -0.03em;
  margin-bottom: 24px;
}
h2 {
  font-size: 45px;
  font-weight: 600;
  line-height: 60px;
  margin-bottom: 24px;
}
h3 {
  font-size: 24px;
  font-weight: 600;
  line-height: 40px;
  margin-bottom: 24px;
}
h4 {
  font-size: 22px;
  font-weight: 600;
  line-height: 35px;
  margin-bottom: 16px;
}
h5 {
  font-size: 20px;
  font-weight: 600;
  line-height: 30px;
}
h6 {
  font-size: 18px;
  font-weight: 600;
  line-height: 35px;
}
p {
  color: var(--caprim-primary-gunmetal-gray);
  margin: 0 0 16px;
}
input:focus,
select:focus,
textarea:focus {
  box-shadow: none !important;
}
textarea:focus-visible {
  outline: 0;
}
input:-webkit-autofill {
  -webkit-background-clip: text;
}
[type="search"] {
  outline-offset: 0;
}
input:focus-visible,
button:focus-visible {
  outline: 0;
}
.white-font,
.white-font a {
  color: var(--caprim-secondary-beige);
}
.gutter-y-10 {
  --bs-gutter-y: 10px;
}
.gutter-y-15 {
  --bs-gutter-y: 15px;
}
.gutter-y-20 {
  --bs-gutter-y: 20px;
}
.gutter-y-30 {
  --bs-gutter-y: 30px;
}
.gutter-y-40 {
  --bs-gutter-y: 40px;
}
.gutter-y-60 {
  --bs-gutter-y: 60px;
}
.gutter-x-15 {
  --bs-gutter-x: 30px;
}
.mb-40 {
  margin-bottom: 40px;
}
/***********************
* Button style 
***********************/
.btn-primary {
  background: var(--caprim-primary-coral-orange);
  column-gap: 10px;
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  border: none;
  line-height: 30px;
  padding: 8px 30px;
  border-radius: 30px;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  z-index: 1;
}
.btn-secondary {
  background-color: transparent;
  column-gap: 10px;
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  border: 1px solid var(--caprim-primary-coral-orange);
  color: var(--caprim-primary-coral-orange);
  line-height: 30px;
  padding: 8px 30px;
  border-radius: 30px;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  z-index: 1;
}
.btn-outline-secondary {
  column-gap: 10px;
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  line-height: 30px;
  border-radius: 30px;
  padding: 8px 30px;
  background-color: transparent;
  border: 1px solid var(--caprim-primary-coral-orange);
  color: var(--caprim-primary-coral-orange);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  z-index: 1;
}
.btn-outline-primary {
  column-gap: 10px;
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  line-height: 30px;
  border-radius: 30px;
  padding: 8px 30px;
  background-color: transparent;
  color: var(--loanlift-text-light);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  z-index: 1;
}
.btn-light {
  background: transparent;
  column-gap: 10px;
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  border: 1px solid var(--caprim);
  line-height: 30px;
  padding: 8px 30px;
  border-radius: 30px;
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  z-index: 1;
  color: var(--caprim-secondary-yellow);
}
.btn-primary i,
.btn-secondary i,
.btn-outline-secondary i,
.btn-outline-primary i,
.btn-light i {
  display: flex;
}
.btn-primary::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--caprim-secondary-yellow);
  color: var(--caprim-primary-coral-orange);
  z-index: -1;
  height: 140px;
  width: 230px;
  transform: translate(-167px, -110px) rotate(-34deg);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.btn-secondary::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--caprim-primary-coral-orange);
  color: var(--caprim-secondary-beige);
  z-index: -1;
  height: 140px;
  width: 230px;
  transform: translate(-167px, -110px) rotate(-34deg);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.btn-outline-secondary::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--caprim-primary-coral-orange);
  color: var(--caprim-secondary-beige);
  z-index: -1;
  height: 140px;
  width: 230px;
  transform: translate(-167px, -110px) rotate(-34deg);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.btn-outline-primary::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--loanlift-text-light);
  color: var(--loanlift-text-black);
  z-index: -1;
  height: 140px;
  width: 230px;
  transform: translate(-167px, -110px) rotate(-34deg);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.btn-light::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--caprim-secondary-yellow);
  color: var(--caprim-primary-coral-orange);
  z-index: -1;
  height: 140px;
  width: 230px;
  transform: translate(-167px, -110px) rotate(-34deg);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
.btn-primary:hover {
  background-color: transparent;
  border-color: transparent;
  color: var(--caprim-primary-gunmetal-gray);
}
.btn-secondary:hover {
  color: var(--caprim-secondary-beige);
  border-color: var(--caprim-primary-coral-orange);
}
.btn-outline-secondary:hover {
  background-color: var(--caprim-primary-coral-orange);
  color: var(--loanlift-text-light);
  border: var(--caprim-primary-coral-orange);
}
.btn-outline-primary:hover {
  background-color: var(--loanlift-text-light);
  color: var(--loanlift-text-black);
  border-color: var(--caprim-primary-coral-orange);
}
.btn-light:hover {
  background-color: var(--loanlift-text-black);
  color: var(--caprim-primary-gunmetal-gray);
}
.btn-primary i,
.btn-secondary i,
.btn-outline-secondary i,
.btn-outline-primary i,
.btn-light i {
  transition: transform 300ms ease;
  -moz-transition: transform 300ms ease;
  -webkit-transition: transform 300ms ease;
  -ms-transition: transform 300ms ease;
  -o-transition: transform 300ms ease;
}
.btn-primary:hover i,
.btn-secondary:hover i,
.btn-outline-secondary:hover i,
.btn-outline-primary:hover i,
.btn-light:hover i {
  transform: rotateZ(-45deg);
}
.btn-primary:hover::after,
.btn-secondary:hover::after,
.btn-outline-secondary:hover::after,
.btn-outline-primary:hover::after,
.btn-light:hover::after {
  transform: translate(-0px, -60px) rotate(-34deg);
}
.btn-small {
  font-size: 14px;
  line-height: 20px;
  padding: 10px 15px;
}
.more-btn {
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 0;
  border-radius: 100%;
  border: none;
  background-color: rgba(255, 255, 255, 0.2);
}
.btn-check:checked + .btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check) + .btn:active {
  background-color: transparent;
  border-color: var(--loanlift-text-light);
  color: inherit;
}

/***********************
 * scroll to top 
***********************/
.scroll-to-top {
  display: flex;
  align-items: center;
  width: auto;
  height: 35px;
  background: transparent;
  position: fixed;
  bottom: 60px;
  right: -12px;
  z-index: 99;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transform: rotate(-90deg);
  cursor: pointer;
  transition: all 0.2s ease;
}
.scroll-to-top-text {
  display: inline;
  font-size: 12px;
  margin-left: 10px;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 700;
  margin-right: 8px;
  color: var(--caprim-primary-gunmetal-gray);
}
.scroll-to-top-wrapper {
  display: inline-block;
  width: 30px;
  height: 4px;
  background-color: var(--caprim-primary-coral-orange);
  position: relative;
  overflow: hidden;
}
.scroll-to-top-inner {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--caprim-primary-coral-orange);
}
.scroll-to-top.show {
  opacity: 1;
  visibility: visible;
  bottom: 70px;
}
/***********************
 * header one
***********************/
.topbar-one {
  background-color: var(--loanlift-primary-bg);
  color: #ffffff;
  margin-left: 303px;
  position: absolute;
  z-index: 2;
  width: -webkit-fill-available;
  top: 0;
}
.topbar-one-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 40px;
}
ul.topbar-one-info {
  display: flex;
  align-items: center;
  column-gap: 23px;
}
ul.topbar-one-info li {
  display: flex;
  align-items: center;
  column-gap: 14px;
}
ul.topbar-one-info li i {
  display: flex;
  font-size: 20px;
}
ul.topbar-one-info li p {
  margin-bottom: 0;
}
ul.topbar-one-info li p,
ul.topbar-one-info li a {
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.8);
}
.topbar-one-right {
  display: flex;
  align-items: center;
  column-gap: 40px;
}
.topbar-one-right ul {
  display: flex;
  align-items: center;
  column-gap: 25px;
}
.topbar-one-right ul li {
  line-height: 0;
}
.topbar-one-right ul li a {
  color: rgba(255, 255, 255, 0.8);
  line-height: 20px;
  font-size: 14px;
}
.main-header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  border-bottom: 1px solid #fff3;
}
nav.main-menu {
  display: flex;
  align-items: center;
}
.main-menu-logo {
  max-width: 290px;
  width: 100%;
  position: relative;
  text-align: center;
}
.main-menu-inner {
  display: flex;
  width: 100%;
  padding-top: 50px;
}
ul.main-menu-list {
  display: flex;
  align-items: center;
  column-gap: 44px;
  padding: 0px 30px;
  padding-left: 56px;
}
ul.main-menu-list li {
  display: flex;
  column-gap: 8px;
  align-items: center;
  color: var(--loanlift-text-light);
  position: relative;
  padding: 27px 0;
}
ul.main-menu-list li a,
ul.main-menu-list li {
  color: var(--loanlift-text-light);
  transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
}
ul.main-menu-list li ul {
  position: absolute;
  width: 230px;
  padding: 10px 0;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  transform-origin: top center;
  transform: scaleY(0) translateZ(100px);
  transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease;
  z-index: 999;
  background-color: var(--loanlift-text-light);
  border-bottom: 2px solid var(--loanlift-primary-bg);
  box-shadow: 0 0 60px 0 rgb(53 57 69 / 15%);
}
ul.main-menu-list li:hover ul {
  opacity: 1;
  transform: scaleY(1) translateZ(0px);
  visibility: visible;
}
ul.main-menu-list li ul li {
  padding: 10px 25px;
}
ul.main-menu-list li ul li a {
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: var(--caprim-primary-gunmetal-gray);
  position: relative;
}
ul.main-menu-list li ul li a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  height: 1px;
  width: 0px;
  background-color: var(--caprim-primary-gunmetal-gray);
  transition: all 0.5s;
}
ul.main-menu-list li ul li:hover a::after {
  width: 100%;
}
.main-menu-right {
  margin-left: auto;
  margin-right: 40px;
  display: flex;
  align-items: center;
  column-gap: 20px;
}
.search-btn {
  border: none;
  background: none;
  color: var(--caprim-primary-gunmetal-gray);
  font-size: 20px;
  line-height: 20px;
  transition: all 0.5s;
}
.search-btn:hover {
  color: var(--caprim-primary-coral-orange);
}
.main-menu-logo::after {
  content: "";
  position: absolute;
  bottom: -51px;
  right: -1px;
  height: 144px;
  width: 1px;
  background-color: #fff3;
}
.search-filed.search-active {
  max-width: 1000px;
  opacity: 1;
}
.search-btn.search-active {
  font-size: 20px;
  line-height: 20px;
}
.header-right-end {
  position: relative;
  width: 28px;
  height: 21px;
  margin-left: auto;
  cursor: pointer;
  margin-right: 30px;
  display: none;
}
.header-right-end span:nth-child(1) {
  position: absolute;
  width: 20px;
  height: 3px;
  background-color: var(--loanlift-text-light);
  border-radius: 10px;
  left: 0;
  top: 0;
}
.header-right-end span:nth-child(2) {
  position: absolute;
  width: 20px;
  height: 3px;
  background-color: var(--loanlift-text-light);
  border-radius: 10px;
  right: 0;
  bottom: 9px;
}
.header-right-end span:nth-child(3) {
  position: absolute;
  width: 20px;
  height: 3px;
  background-color: var(--loanlift-text-light);
  border-radius: 10px;
  left: 0;
  bottom: 0;
}
.sticky-header--cloned.sticky-fixed .main-menu-inner {
  padding-top: 0;
}
.sticky-header--cloned.sticky-fixed .main-menu-logo::after {
  height: 84px;
  bottom: -26px;
}
/***********************
 * main-header-two
***********************/
.topbar-two {
  background-color: #00070f;
  padding: 21px 0;
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 2;
}
.topbar-two p {
  margin-bottom: 0;
  line-height: 18px;
  color: var(--loanlift-text-light);
}
ul.topbar-two-social-media {
  display: flex;
  column-gap: 30px;
  align-items: center;
  justify-content: end;
}
ul.topbar-two-social-media li {
  display: flex;
}
ul.topbar-two-social-media li a {
  color: rgba(255, 255, 255, 0.8);
  line-height: 18px;
  font-size: 15px;
}
ul.topbar-two-social-media li a:hover {
  color: #979795;
}
.main-header-two {
  border-bottom: 1px solid var(--loanlift-text-light-grey);
  background-color: transparent;
  padding-top: 60px;
}
.main-header-two .main-menu-inner {
  padding: 0;
  justify-content: center;
}
.main-header-two .main-menu-logo::after {
  display: none;
}
.main-header-two .main-menu-right {
  margin-right: 0;
  flex-shrink: 0;
}
.main-header-two .search-btn {
  font-size: 25px;
  line-height: 17px;
}
.menu-phone {
  display: flex;
  align-items: center;
  column-gap: 14px;
  color: var(--caprim-secondary-beige);
  font-size: 18px;
  line-height: 35px;
  letter-spacing: -0.02em;
  font-family: var(--caprim-heading-font);
}

.menu-phone i {
  font-size: 25px;
  line-height: 17px;
}
.main-header-two ul.main-menu-list > li {
  padding: 30px 0;
}
.main-header-two.sticky-header--cloned.sticky-fixed {
  padding-top: 0;
}
.main-header-two .main-menu-right span {
  width: 1px;
  height: 51px;
  background-color: rgba(255, 255, 255, 0.8);
  display: inline-block;
}
/***********************
 * main-header-three
***********************/
.main-header-three {
  backdrop-filter: blur(0px);
  box-shadow: none;
}
.main-header-three nav.main-menu {
  margin: 0 68px;
}
.main-header-three .main-menu-inner {
  padding-top: 0;
  justify-content: center;
}
.main-header-three .main-menu-logo::after {
  display: none;
}
.main-header-three ul.main-menu-list > li {
  padding: 35px 0;
}
.main-header-three .main-menu-right {
  margin-right: 30px;
  flex-shrink: 0;
  column-gap: 40px;
}
ul.contact-info {
  display: flex;
  align-items: center;
  column-gap: 14px;
}
ul.contact-info li {
  display: flex;
  align-items: center;
  color: var(--loanlift-text-light);
  column-gap: 14px;
}
ul.contact-info li i {
  display: flex;
  font-size: 27px;
}
ul.contact-info li a {
  color: var(--loanlift-text-light);
}
