/***********************
 * search-popup
***********************/
.search-popup {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  opacity: 0;
  z-index: 99999;
  visibility: hidden;
  transform: scale(0.5, 0.5);
  background-color: rgba(0, 0, 0, 0.7);
  -webkit-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  transition: all 500ms ease;
}
.search-active .search-popup {
  transform: translateY(0%);
  margin-top: 0;
  opacity: 1;
  visibility: visible;
  transform: scale(1, 1);
}
.close-search {
  position: absolute;
  left: 0;
  right: 0;
  border: transparent;
  top: 20%;
  margin: 0 auto;
  border-radius: 50%;
  text-align: center;
  background-color: var(--loanlift-text-light);
  width: 50px;
  height: 50px;
  cursor: pointer;
  -webkit-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  opacity: 0;
  visibility: hidden;
}
.search-active .close-search {
  visibility: visible;
  opacity: 1;
}
.close-search:before,
.close-search:after {
  content: "";
  position: absolute;
  height: 2px;
  background-color: var(--loanlift-text-black);
  width: 25px;
  top: 24px;
  right: 13px;
  transform: rotate(45deg);
}
.close-search:after {
  transform: rotate(-45deg);
}
.search-popup form {
  position: absolute;
  max-width: 700px;
  top: 50%;
  left: 15px;
  right: 15px;
  margin: 0 auto 0;
  transform-origin: center;
  background-color: var(--loanlift-text-light);
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  -ms-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}
.search-popup .form-group {
  position: relative;
  margin: 0px;
  overflow: hidden;
}
.search-popup .form-group input[type="search"] {
  position: relative;
  display: block;
  font-size: 18px;
  line-height: 50px;
  color: var(--loanlift-text-black);
  height: 70px;
  width: 100%;
  padding: 10px 30px;
  background-color: var(--loanlift-text-light);
  -webkit-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  font-weight: 500;
  text-transform: capitalize;
  border: none;
}
.search-popup .form-group input[type="submit"],
.search-popup .form-group button {
  position: absolute;
  right: 30px;
  top: 3px;
  height: 70px;
  line-height: 30px;
  background: transparent;
  text-align: center;
  font-size: 30px;
  color: var(--loanlift-text-black);
  padding: 0;
  cursor: pointer;
  -webkit-transition: all 500ms ease;
  -moz-transition: all 500ms ease;
  -ms-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  border: none;
}
/***********************
 * mobile-nav
***********************/
.mobile-nav-wrapper {
  position: fixed;
  z-index: 991;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  transform-origin: left center;
  transition: all 0.5s;
  visibility: hidden;
  z-index: 999999;
}
.mobile-nav-wrapper.expanded {
  opacity: 1;
  transform: translateX(0%);
  visibility: visible;
  transition: all 0.5s;
}
.mobile-nav-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #00000090;
  opacity: 0.9;
}
.mobile-nav-content {
  width: 100%;
  max-width: 300px;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  overflow-y: auto;
  height: 100%;
  background-color: var(--caprim-secondary-blue-light);
  -ms-overflow-style: none;
  scrollbar-width: none;
  padding: 30px 15px;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-100%);
  transition: all 0.5s;
}
.mobile-nav-wrapper.expanded .mobile-nav-content {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  transition: all 0.5s;
}
.mobile-nav-close {
  width: 25px;
  height: 25px;
  display: block;
  position: absolute;
  top: 42px;
  right: 15px;
}
.mobile-nav-close span {
  width: 3px;
  height: 100%;
  display: block;
  position: absolute;
  border-radius: 10px;
  top: 50%;
  left: 50%;
  background-color: var(--loanlift-text-light);
  transform: translate(-50%, -50%) rotate(45deg);
}
.mobile-nav-close span:nth-child(2) {
  transform: translate(-50%, -50%) rotate(-45deg);
}
