/***********************
 * about one
***********************/
.about-one {
  padding: 100px 0;
  position: relative;
  padding-bottom: 0px;
}
.about-one .heading-title {
  margin-bottom: 40px;
}

.about-one-shape-1 {
  position: absolute;
  top: 50px;
  right: 50px;
}
.about-one-shape-2 {
  position: absolute;
  bottom: 10px;
  left: 50px;
}
.about-one-image {
  height: 100%;
}
.about-one-image,
.why-choose-two-image {
  position: relative;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  overflow: hidden;
  transition: 2s 0.5s cubic-bezier(0.5, 0.5, 0, 1);
}
.about-one-image img {
  height: 100%;
}
.about-one-image img,
.why-choose-two-image img {
  transform: scale(1.5) translate(0, -100px);
  transform-origin: 50% 50%;
  transition: 2s 0.5s cubic-bezier(0.5, 0.5, 0, 1);
}
.about-one-image.active,
.why-choose-two-image.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.about-one-image.active img,
.why-choose-two-image.active img {
  transform: scale(1) translate(0, 0);
}
.about-one_image {
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
  overflow: hidden;
  transition: 2s 0.5s cubic-bezier(0.5, 0.5, 0, 1);
  position: relative;
}
.about-one_image.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.about-one_image img {
  transform: scale(1.5) translate(0, 100px);
  transform-origin: 50% 50%;
  transition: 2s 0.5s cubic-bezier(0.5, 0.5, 0, 1);
}
.about-one_image.active img {
  transform: scale(1) translate(0, 0);
}

