/***********************
 * animation
***********************/
.zoom-in {
  animation: zoom 15s linear;
}
.fade-left {
  animation: fade-left 1s linear;
}
@keyframes fade-left {
  0% {
    opacity: 0;
    transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes fade-in-up {
  0% {
    opacity: 0;
    transform: translateY(200px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes zoom {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.25);
  }
}
@keyframes zoom-in-out {
  0% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.5);
  }
}
@keyframes fade-up-in {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-70px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes pulse-ring {
  0% {
    width: 62px;
    height: 62px;
    opacity: 1;
  }
  100% {
    width: 90px;
    height: 90px;
    opacity: 0;
  }
}
@keyframes pulse-ring-2 {
  0% {
    width: 128px;
    height: 128px;
    opacity: 1;
  }
  100% {
    width: 190px;
    height: 190px;
    opacity: 0;
  }
}
@keyframes swipe {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes textRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotet {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(306deg);
  }
}
@keyframes fale-left-two {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }
  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes fale-right-two {
  0% {
    opacity: 0;
    transform: translateX(150px);
  }
  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}
@keyframes underline {
  0% {
    background-size: 100% 100%;
    background-position-x: 100%;
  }

  100% {
    background-size: 0 100%;
    background-position-x: 100%;
  }
}
@keyframes underline-hover {
  0% {
    background-position-x: 0;
    background-size: 0 100%;
  }

  100% {
    background-position-x: 0;
    background-size: 100% 100%;
  }
}
@keyframes moveLetters {
  0% {
    transform: translateX(-15vw);
    opacity: 0;
  }
  33.3%,
  66% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(15vw);
    opacity: 0;
  }
}
